export type C64Fn = 'stop' | 'shOff' | 'shOn' | 'rtrn' | 'lCase' | 'down' | 'rvsOn' | 'home' | 'del' | 'right' | 'run' | 'f1' | 'f3' | 'f5' | 'f7' | 'f2' | 'f4' | 'f6' | 'f8' | 'shRtrn' | 'uCase' | 'up' | 'rvsOff' | 'clr' | 'inst' | 'left' | C64Color;
export type C64StringFn = { type: 'tab' | 'spc', value: number };
export type C64String = string | number | C64StringFn | Array<string | number | C64StringFn>;

