export const bcksp: number = 0x100; // Backspace

export const findPokeIndex = (pokeMap: C64PokeMap, char: string): number => {
  const index: string | undefined = Object.getOwnPropertyNames(pokeMap).find(index => pokeMap[parseInt(index)] === char);
  return index !== undefined && parseInt(index) || 0x3f;
};

export const stringToNumberArray = (pokeMap: C64PokeMap, s: string): number[] => [...s].map(char => findPokeIndex(pokeMap, char));

export const flattenMixedArray = (pokeMap: C64PokeMap, a: Array<string | number | C64StringFn>): Array<number | C64StringFn> => {
  const result: Array<number | C64StringFn> = [];
  a.forEach(elem => typeof(elem) === 'string' ? result.push(...stringToNumberArray(pokeMap, elem)) : result.push(elem));
  return result;
};

export const c64StringToNumberArray = (pokeMap: C64PokeMap, text: C64String): Array<number | C64StringFn> => {
  return typeof(text) === 'string' ? stringToNumberArray(pokeMap, text) : typeof(text) === 'number' || (typeof(text) === 'object' && !Array.isArray(text)) ? [text] : flattenMixedArray(pokeMap, text);
};

